.card {
  position: relative;
}
.card img {
  width: 150px;
  height: 150px;
  display: block;
  border: 2px solid white;
  border-radius: 6px;
}

.card img.back {
  object-fit: cover;
}

.card .front {
  transform: rotateY(90deg);
  transition: all ease-in 0.2s;
  position: absolute;
  background-color: rgb(152, 149, 149);
}

.flipped .front{
  transform: rotateY(0deg);
  transition-delay: 0.2s;
}

.card .back{
  transition: all ease-in 0.2s;
  transition-delay: 0s;
}

.flipped .back{
  transform: rotateY(90deg);
  transition-delay: 0s;
}
